@-webkit-keyframes draw {
    0% {
        stroke-dashoffset: 280;
    }
    100% {
        stroke-dashoffset: 0;
    }
}

.btn {
    display: inline-block;
    border: none;
    background: none;
    padding: 0;
    margin: 0;
    font-size: inherit;
    cursor: pointer;
    color: inherit;
}

.btn-print {
    img {
        width: 35px;
        height: 35px;

        transition: 0.25s ease-in-out all;

        @media (max-width: $md2) {
            width: 30px;
            height: 30px;
        }

        @media (any-hover: hover) {
            &:hover {
                transform: scale(1.1);
                fill: #999;
            }
        }
    }
}
