@import "fonts";
@import "libs/reset";
@import "libs/vars";

*,
*:before,
*:after {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html {
    font-size: 62.5%; // Адаптивный шрифт
    overflow-x: hidden;
}

body {
    color: #000; // Стандартный цвет шрифта
    font-size: 1.4rem; // Стандартный размер шрифта
    font-family: "Roboto";
    font-weight: 400;
    line-height: 1.3;

    &::-webkit-scrollbar {
        width: 0px;
    }

    overflow-x: hidden;
    -webkit-tap-highlight-color: transparent !important;

    transition: 0.25s ease-in-out all;
}

._container {
    max-width: 1400px;
    padding: 0px 15px;
    margin: 0 auto;
}

.main {
    min-height: 70vh;
    @media (max-width: $md1) {
        margin-top: 73px;
        padding-top: 40px;
    }
}

body::-webkit-scrollbar {
    width: 0; /* Для Chrome, Safari и Opera */
}

// UI
@import "components/UI/title";
@import "components/UI/loader";
@import "components/UI/underline-anim";
@import "components/UI/btn";

