.footer {
    background-color: #f2f2f2;
    margin-top: 70px;
    @media (max-width: $md2) {
        margin-top: 40px;
        padding-bottom: 30px;
        padding-top: 10px;
    }
    // .footer__content
    &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @media (max-width: $md2) {
            flex-direction: column;
            gap: 30px;
        }
    }
    &__nav-list {
        @media (max-width: $md2) {
            gap: 10px;
            flex-wrap: wrap;
            justify-content: center;
        }
    }
    // .footer__logo
    &__logo {
        width: 150px;
        height: 150px;
    }
    // .footer__nav
    &__nav {
    }
    // .footer__copyright
    &__copyright {
    }
}
