.hero {
    // .hero__title
    &__title {
        font-size: 55px;
        margin-bottom: 10px;
        font-weight: 500;

        span {
            color: $blue;
            margin-top: 10px;
            font-size: 65px;

            @media (max-width: $md2) {
                font-size: 45px;
            }
        }

        @media (max-width: $md2) {
            font-size: 40px;
            margin-bottom: 25px;
        }
    }
    // .hero__subtitle
    &__subtitle {
        font-size: 37px;
        line-height: 1.5;
        font-weight: 500;

        @media (max-width: $md2) {
            font-size: 30px;
        }
    }
    &__text {
        margin-top: 50px;
        font-size: 22px;
    }
}
